'use strict';

import angular from 'angular';
import 'angular-material';
import mainwrap from 'common/directives/mainwrap/mainwrap';
import configService from 'config/config';
import NoncommercialTermsCtrl from './noncommercialTerms.controller';
import './noncommercialTerms.tpl';
import './noncommercialTerms.css!';

const noncommercialTermsModule = angular.module('noncommercialTerms', [
  mainwrap.name,
  configService.name,
  'app/noncommercialTerms/noncommercialTerms.tpl.html',
]);

noncommercialTermsModule.config( ($stateProvider) => {
  $stateProvider
    .state('noncommercialTerms', {
      url: '/noncommercial-terms',
      templateUrl: 'app/noncommercialTerms/noncommercialTerms.tpl.html',
      controller: NoncommercialTermsCtrl,
      controllerAs: 'noncommercialTermsCtrl'
    });
});

export default noncommercialTermsModule;
